$confirmed-color: #448aff;
$death-color: #ff5252;
$recovered-color: #b2ff59;
$active-color: #ffff00;

.App {
  text-align: center;
}

.main-container {
  background-color: #00002d;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: #eff5fc;

  .header {
    margin-top: 10px;
    .title {
      font-size: 50px;
      font-weight: bold;
      margin-left: 160px;
    }
    .date {
      float: right;
      font-size: 24px;
      line-height: 66px;
      font-weight: 400;
      margin-right: 30px;
    }
  }
  
  .display-flex {
    display: flex;
  }

  
  .card-block {
    width: 50%;
    padding: 0 15px;
    margin: 25px 0;
    border-right: 1px solid whitesmoke;
    &.no-boder {
      border: 0;
    }
    .card-container-title {
      margin-bottom: 20px;
      font-size: 40px;
      font-weight: 200;
    }
    .card-container {
      display: flex;
      .cards {
        margin: 0 10px;
        border-bottom: 2px solid whitesmoke;
        border-radius: 6px;
        width: 25%;
        height: 100px;
        background: #11093f;
        .count {
          font-size: 40px;
          font-weight: 600;
          color: white;
        }
        .confirmed-card {
          color: $confirmed-color;
          font-weight: 500;
        }
      
        .death-card {
          color: $death-color;
          font-weight: 500;
        }
      
        .recovered-card {
          color: $recovered-color;
          font-weight: 500;
        }
      
        .active-card {
          color: $active-color;
          font-weight: 500;
        }
        // &:hover {
        //   border: 2px solid whitesmoke;
        // }
      }
    }
  }

  .details-container {
    display: flex;
    .left-panel{
      width: 20%;
      background: #11093f;
      margin-left: 25px;
      border-radius: 6px;
      .country-list {
        height: 345px;
        overflow-y: auto;
        .country-row {
          font-size: 18px;
          text-align: left;
          border-bottom: 1px solid whitesmoke;
          padding: 12px 24px;
          &:hover {
            color: $confirmed-color;
            cursor: pointer;
          }
        }
      }
    }
    .right-panel {
      width: 80%;
      .chart-container {
        width: 90%;
        margin-left: 40px;
        margin-top: 20px;
      }
    }
  }
  .footer {
    background-color: #00002d;
    color: #448aff;
    line-height: 30px;
    .margin-15 {
      margin-left: 15%;
    }
    .margin-left-20 {
      margin-left: 20px;
    }
    .credits {
      text-align: left;
      display: flex;
      font-size: 17px;
      color: #eff5fc;
      margin-left: 25px;
      .made-by {
        font-size: 16px;
        float: right;
        margin-right: 10px;
        color: #448aff;
        i {
          margin-left: 4px;
          color: #448aff;
        }
        .love {
          font-size: 12px;
          color: red;
        }
      }
    }
  }
}

